$dark-gray: #1c2227;
$light-blue: #45c8f2;

@import "../components/addons";

$red-blue: linear-gradient( rgba(52, 93, 241, 0.45), rgba(255, 0, 0, 0.45) );
$purple-yellow: linear-gradient( rgba(148, 103, 103, 0.50), rgba(114, 54, 255, 0.50) );
$purple-yellow2: linear-gradient( rgba(235, 51, 51, 0.50), rgba(194, 168, 255, 0.50) );
$teal-purple: linear-gradient( rgba(51, 230, 235, 0.50), rgba(186, 63, 141, 0.50) );
$orange-yellow: linear-gradient( rgba(235, 122, 51, 0.50), rgba(186, 182, 63, 0.50) );
$purple-teal: linear-gradient( rgba(107, 51, 235, 0.50), rgba(63, 167, 186, 0.50) );

#index {
  .article-banners {
    font-size: 0;
    @media screen and (max-width: 1024px) {
      height: 525px;
      width: 100%;
      display: block; }
    @media screen and (min-width: 1025px) {
      height: 350px;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap; }
    a {
      font-size: 0;
      margin: 0;
      @media screen and (max-width: 599px) {
        display: none;
        width: 100%;
        height: 175px;
        &:nth-child(1), &:nth-child(2), &:nth-child(3) {
          display: block; } }
      @media screen and (min-width: 600px) and (max-width: 799px) {
        width: 50%;
        &:nth-last-child(1) {
          display: none; } }
      @media screen and (min-width: 600px) and (max-width: 1024px) {
        height: 175px;
        @media screen and (max-width: 799px) {
          width: 50%;
          &:nth-last-child(1) {
            display: none; } }
        @media screen and (min-width: 800px) {
          width: 33.33333%; }
        //width: 50%
        display: inline-block;
        &:nth-child(1), &:nth-child(2) {
          margin: 0;
          width: 50%;
          height: 350px; } }
      @media screen and (min-width: 1025px) {
        width: 33.33333%;
        height: 175px;
        &:nth-child(1) {
          height: 350px; } }
      > div {
        position: absolute;
        width: inherit;
        height: inherit;
        &.gradient {
          z-index: 10;
          &.color1 {
            background-image: $red-blue; }
          &.color2 {
            background-image: $purple-teal; }
          &.color3 {
            background-image: $purple-yellow2; }
          &.color4 {
            background-image: $orange-yellow; }
          &.color5 {
            background-image: $teal-purple; }
          @extend .linear-transition;
          p {
            margin: 0;
            color: white;
            display: table;
            position: relative;
            left: 10px;
            top: 10px;
            font-size: 1.3em;
            width: 90%; } }
        &.banner {
          margin: 0;
          background-image: url('../images/articles/small1.png');
          background-position: center;
          background-repeat: no-repeat;
          background-size: cover;
          cursor: pointer; } }
      &:hover {
        .gradient {
          box-shadow: inset 0 0 200px 200px rgba(0, 0, 0, 0.15);
          cursor: pointer; } } } }

  .categories {
    height: 60px;
    width: 100%;
    overflow-x: auto;
    white-space: nowrap;
    @media screen and (min-width: 600px) {
      width: 100%; }
    background-color: $dark-gray;
    font-size: 0;
    .area {
      height: inherit;
      width: 38%;
      @media screen and (min-width: 500px) {
        width: 28%; }
      @media screen and (min-width: 700px) {
        width: 21.5%; }
      @media screen and (min-width: 800px) {
        width: calc(100%/6); }
      display: inline-block;
      text-align: center;
      @extend .no-user-select;
      @extend .linear-transition;
      .icon {
        display: inline-block;
        height: 35px;
        width: 35px;
        -webkit-mask: url('../images/categories/science.svg') no-repeat 50% 50%;
        mask: url('../images/categories/science.svg') no-repeat 50% 50%;
        background-color: $light-blue;
        background-size: contain;
        position: relative;
        top: 20%; }
      p {
        color: $light-blue;
        font-size: 18px;
        display: inline;
        padding-left: 5px;
        text-transform: capitalize;
        line-height: 60px; }
      &:hover {
        cursor: pointer;
        background-color: $light-blue;
        .icon {
          background-color: $dark-gray; }
        p {
          color: $dark-gray; } }
      &:active {
        background-color: white;
        .icon {
          background-color: black; }
        p {
          color: black; } } } }

  .article-posts {
    height: auto;
    width: 100%;
    font-size: 0;
    display: block;
    .left {
      vertical-align: top;
      display: inline-block;
      width: 100%;
      height: 100%;
      font-size: 0;
      height: inherit;
      @media screen and (min-width: 1000px) {
        width: 75%; }
      .article {
        @extend .linear-transition;

        position: relative;
        width: 95%;
        margin: 0;
        font-size: 0;
        margin: 30px auto 0 auto;
        &:hover {
          cursor: pointer;
          transform: translateX(10px);
          box-shadow: inset 0 0 200px 200px rgba(0, 0, 0, 0.05);
          .image {
            box-shadow: inset 0 0 200px 200px rgba(0, 0, 0, 0.05); } }
        //height: 180px
        //max-height: 80vmin
        @media screen and (min-width: 800px) {
          height: 20vw;
          margin: 30px 0px 30px 30px;
          width: 97%; }
        > div {
          display: block;
          @media screen and (min-width: 800px) {
            display: inline-block; }
          &.image {
            height: 50vw;
            width: 100%;
            @media screen and (min-width: 800px) {
              height: inherit;
              width: 33%; }

            background-image: url('../images/articles/article1.png');
            background-size: cover;
            background-position: center;
            margin-right: 30px; }
          &.info {
            vertical-align: top;
            width: 97%;
            margin-top: 10px;
            @media screen and (min-width: 800px) {
              width: 60%;
              margin-top: 0; }
            font-size: 0;
            p {
              margin: 10px 0;
              &.category {
                color: $light-blue;
                font-weight: bold;
                margin-top: 0;
                text-transform: capitalize; }
              &.title {
                //font-size: 20px
                color: black;
                font-weight: bold;
                font-size: 30px;
                @media screen and (min-width: 800px) {
                  font-size: 22px; }
                @media screen and (min-width: 1300px) {
                  font-size: 26px; } }
              &.intro {
                font-size: 15px;
                color: black;
                @media screen and (min-width: 800px) {
                  font-size: 16px; }
                @media screen and (min-width: 1300px) {
                  font-size: 20px; } } } } } } }

    .right {
      display: none;
      .article .window {
        @extend .linear-transition;
        @extend .no-user-select; }

      @media screen and (min-width: 1000px) {
        vertical-align: top;
        display: inline-block;
        width: 25%;
        font-size: 0;
        .container {
          width: auto;
          float: right;
          margin-right: 30px;
          > p {
            font-weight: bold;
            font-size: 2vw; }
          .article {
            height: 10vw;
            width: 20vw;
            background-image: url('../images/articles/article2.png');
            background-size: cover;
            background-position: center;
            margin-bottom: 30px;
            position: relative;
            overflow: hidden;
            &:hover {
              cursor: pointer;
              .window {
                transform: translate(-40px, -50%); } }
            .window {
              width: 70%;
              height: 60px;
              @media screen and (min-width: 1350px) {
                height: 50%; }
              background-color: rgba(28, 34, 39, 0.69);
              font-size: 0;
              position: absolute;
              top: 50%;
              transform: translateY(-50%);

              display: flex;
              flex-direction: row;
              align-items: center;
              flex-wrap: nowrap;
              > p {
                font-size: 1.2vw;
                @media screen and (min-width: 1350px) {
                  font-size: 16px; }
                @media screen and (min-width: 1500px) {
                  font-size: 18px; }
                color: white;
                margin-top: 0;
                margin-bottom: 0;
                margin-left: 5px;
                display: inline-block;
                width: 77%; }
              > div {
                display: inline-block;
                background-color: $light-blue;
                color: white;
                padding: 5px;
                font-size: 36px;
                height: 45px;
                line-height: 45px;
                width: 40px;
                text-align: center; } } } } } } } }

